<template>
  <div id="chart">
    <div class="message">{{msg}}</div>
    <button class="danger" @click="clear">clear</button>
    <div id="uv">
      <p id="title">Heart Rate</p>
      <p id="number">{{uvData | optimize}}</p>
    </div>
    <div id="apex">
      <apexchart type="line" height="350" ref="chart" :options="chartOptions" :series="series"></apexchart>
    </div>
  </div>
</template>

<script>
  import VueApexCharts from 'vue-apexcharts'

  let data = [];
  let lastDate = 0;

function getNewSeries(baseval, uv) {
  var newDate = baseval + 1;
  lastDate = newDate;
  data.push({
    x: newDate,
    y: uv
  });
}

function resetData() {
  data = data.slice(data.length - 10, data.length);
}
	export default {
		props: ['uvData'],
    components: {
      'apexchart': VueApexCharts,
    },
		data() {
      return{
        msg: '',
        uvD: null,
        series: [{
          data: data.slice()
        }],
        chartOptions: {
          chart: {
            id: 'realtime',
            height: 350,
            type: 'line',
            animations: {
              enabled: true,
              easing: 'linear',
              dynamicAnimation: {
                speed: 500
              }
            },
            toolbar: {
              show: true
            },
            zoom: {
              enabled: false
            }
          },
          dataLabels: {
            enabled: false
          },
          stroke: {
            curve: 'smooth'
          },
          title: {
            enabled: false
          },
          markers: {
            size: 0
          },
          xaxis: {
            range: 20,
            show: false
          },
          yaxis: {
            max: 15,
            show: false
          },
          legend: {
            show: false
          },
        },
      }
		},
    watch: {
      uvData() {
        this.uvD = this.uvData;
        // console.log(typeof(this.uvD) + this.uvD);
        if (this.uvD != null && this.uvD != '' && this.uvD < 20) {
          // console.log('here')
          getNewSeries(lastDate, this.uvD)
          this.$refs.chart.updateSeries([{
            data: data
          }])
        }
      }
    },
    mounted() {
    },
    methods: {
    clear() {
      this.text = "";
      this.$socket.close()
    },
    // initWebSocket() {
    //   //初始化weosocket,这些onopen是一个事件，当
    //   this.$socket.onopen = this.websocketonopen;
    //   this.$socket.onerror = this.websocketonerror;
    //   this.$socket.onmessage = this.websocketonmessage;
    //   this.$socket.onclose = this.websocketclose;
    // },
    websocketonopen(e) {
    //  链接ws服务器，e.target.readyState = 0/1/2/3
    //0 CONNECTING ,1 OPEN, 2 CLOSING, 3 CLOSED
      console.log("WebSocket连接成功",e);
      console.log('open');
    },

    websocketonerror(e) {
      //错误
      console.log("WebSocket连接发生错误");
    },
    websocketsend(e) {
      
      //数据发送
      this.$socket.send(JSON.stringify({test:'www.baidu.com'}));
      // this.$socket.close(1000)
      console.log(this.$socket.readyState)//    当前链接状态 https://developer.mozilla.org/zh-CN/docs/Web/API/WebSocket#%E5%B8%B8%E9%87%8F
    },

        websocketonmessage(e) {
      //数据接收
      console.log(JSON.parse(e.data))
      this.msg = 'hello world';
    },
    },
  created() {
    this.initWebSocket();
  },
  destoryed() {
    this.$socket.close();
  },
    filters: {
      optimize: function(value) {
        if (value > 15) {
          value = 0;
        }
        return value;
      }
    }
	}
</script>

<style>
  #chart {
    margin-top: 20px;
  }
  #title {
    font-size: 30px;
  }
  #number {
    font-weight: bold;
    font-size: 40px;
  }
  #uv {
    float: left;
    width: 200px;
    height: 350px;
    background-color: #EFEFF4;
  }
  #apex {
    margin-left: 200px;
  }
</style>